  width: 38px;
  height: 38px;
  position: absolute;
  background: url(../imges/right.svg) no-repeat center;
  background-size: 100% 100%;
  left: -1px;
  top: -1px;
}
.activee::before {
  opacity: 1;
  background: url(../imges/card.png) no-repeat center;
  content: " ";
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}

.scrolled {
